@import url(https://fonts.googleapis.com/css2?family=New+Tegomin&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  min-height: 100vh;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-image: url(/To-Do-List-App/static/media/to-do-background.a9279d48.jpg);
  font-family: 'New Tegomin', serif;
}

.wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  max-width: 630px;
  margin-top: 50px;
  margin-bottom: 50px;
  padding: 30px;
  background-color:rgb(216, 202, 126);
}

.input-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  width: 555px;
}

.input-wrapper input {
  color: rgb(0, 0, 0);
  width: 440px;
  padding: 10px;
  height: 45px;
  margin-left: 10px;
  margin-bottom: 10px;
  background-color: rgb(216, 202, 126);
  border: 1px solid rgb(0, 0, 0);
}
.input-wrapper input::-webkit-input-placeholder {
  color: rgb(0, 0, 0);
}
.input-wrapper input:-ms-input-placeholder {
  color: rgb(0, 0, 0);
}
.input-wrapper input::placeholder {
  color: rgb(0, 0, 0);
}

.input-wrapper button {
  border: 1px solid rgb(255, 255, 255);
  height: 45px;
  width: 95px;
  margin-bottom: 10px;
  background-color: rgb(0, 0, 0);
  color: rgb(255, 255, 255);
  font-size: 19px;
  margin-right: 10px;
}
.input-wrapper button:hover {
  cursor: pointer;
}

.item {
  border: 1px solid rgb(255, 255, 255);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  width: 550px;
  margin: 10px;
  padding: 7px 20px 7px 25px;
  background-color: rgb(0, 0, 0);
}
.item p {
  margin: 0;
  padding: 5px;
  max-width: 500px;
  color: rgb(255, 255, 255);
  font-size: 22px;
}

.delete-button {
  color: rgb(245, 126, 126);
  height: 40px;
  width: 40px;
}
.delete-button:hover {
  cursor: pointer;
}

h1 {
  color: rgb(0, 0, 0);
  font-size: 45px;
  margin-bottom: 30px;
  text-align: center;
}

@media(max-width: 500px){
  .wrapper {
    width: 100%;
  }
  .input-wrapper {
    width: 100%;
  }
  .input-wrapper input {
    width: 100%;
  }
  .item {
    width: 100%;
  }
}
